* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

*::before,
*::after {
  box-sizing: border-box;
}

main {
  display: flex;
}
#style-text {
  display: block;
  height: 100vh;
  width: 50vw;
  background: #1e1e1e;
}
.land-in {
  line-height: 1.5;
  height: 100%;
  width: 100%;
  overflow-y: auto;
  padding: 10px;
  font-size: 1.5em;
}
@media (max-width: 800px){
  main {
    flex-direction: column;
    height: 100vh;
  }
  #style-text {
    width: 100vw;
    height: 50vh;
    /* background-color: red; */
  }
  .land-in {
    line-height: 1.3;
    font-size: 1.2em;
  }
}


@keyframes land-in {
  from {
    opacity: 0;
    transform: translateY(-30%);
  }

  to {
    opacity: 1;
    transform: translateY(0);
  }
}
.pikachu {
  flex-grow: 1;
  background: #ffe600;
  display: block;
  position: relative;
}
.pikachu-wrapper {
  position: relative;
  top: 200px;
}
@media (max-width: 800px) {
  .pikachu-wrapper {
    top: 0;
  }
}
#buttons {
  position: fixed;
  top: 10px;
  right: 20px;
  display: flex;
  flex-direction: column;
}
#buttons button {
  margin-top: 5px;
  outline: none;
  background: whitesmoke;
  border: 1px solid black;
  width: 3.5em;
  height: 2.2em;
  border-radius: 1.1em;
  cursor: pointer;
}
#buttons button:active{
  color: purple;
  border-color: purple;
}
@media (any-hover: hover) {
  #buttons button:hover{
    color: purple;
    border-color: purple;
  }
}

